body {
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.29;
    color: var(--dark-color);
    background-color: var(--btn-text-color);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

html {
    scroll-behavior: smooth;
}

p:last-child {
    margin-bottom: 0;
}

input,
button,
textarea,
select {
    font-family: inherit;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    color: currentColor;
    text-decoration: none;
}

button {
    cursor: pointer;
    border: none;
    outline: none;
    font-family: inherit;
    padding: 0;
    background-color: transparent;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

.is-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

video {
    display: block;
    max-width: 100%;
    height: auto;
}
.container {
    width: 100%;
    max-width: 375px;
    padding: 0 20px;
    margin: 0 auto;
    /*outline: 2px solid blue;*/
}
@media screen and (min-width: 768px) {
    .container {
        max-width: 768px;
        padding: 0 32px;
    }
    .wrapper {
        display: flex;
        gap: 32px;
    }
}

@media screen and (min-width: 1280px) {
    .container {
        max-width: 1280px;
        padding: 0 100px;
    }
    .wrapper {
        display: flex;
        gap: 63px;
    }
}
