body {
  color: var(--dark-color);
  background-color: var(--btn-text-color);
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.29;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

p:last-child {
  margin-bottom: 0;
}

input, button, textarea, select {
  font-family: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  padding: 0;
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

video {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }

  .wrapper {
    gap: 32px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 100px;
  }

  .wrapper {
    gap: 63px;
    display: flex;
  }
}

:root {
  --light-color: #fff;
  --dark-color: #050505;
  --accent-color: #9bb537;
  --stars-color: #eea10c;
  --stars-uncolored: #d9d9d9;
  --btn-text-color: #f8f8f8;
  --transition: scale 300ms linear;
  --light-theme-text-color: #050505;
  --light-theme-semi-transparent-color: rgba(5, 5, 5, .5);
  --light-theme-almost-transparent-color: rgba(5, 5, 5, .3);
  --light-theme-transparent-color: rgba(5, 5, 5, .2);
  --light-theme-slider-semi-transparent-color: rgba(248, 248, 248, .5);
  --dark-theme-background-color: #1e1e1e;
  --cards-background-color-for-stars: rgba(0, 0, 0, .26);
  --animation: 250ms cubic-bezier(.23, .86, .79, .77);
  --ss-content-height: 176px !important;
  --ss-border-radius: 15px !important;
}

.dark {
  background-color: var(--dark-theme-background-color);
  --stars-uncolored: #3c3b3b;
  --light-theme-text-color: #fff;
  --light-theme-semi-transparent-color: rgba(255, 255, 255, .5);
  --light-theme-almost-transparent-color: rgba(255, 255, 255, .3);
  --light-theme-transparent-color: rgba(255, 255, 255, .2);
  --light-modal-text-color: rgba(255, 255, 255, .8);
  --light-modal-underline-color: rgba(255, 255, 255, .5);
  --light-modal-scroll-color: rgba(255, 255, 255, .18);
  --light-modal-background-color: #050505;
}

.base-btn {
  color: var(--btn-text-color);
  background-color: var(--accent-color);
  border: 1px var(--accent-color) solid;
  transition: color var(--animation);
  border-radius: 15px;
  padding: 14px 28px;
}

.base-btn:hover, .base-btn:focus {
  color: var(--dark-color);
  outline: none;
}

:root {
  --white-2: #f3f3f3;
  --switch-light-grey: #cecdcd;
  --primary-brand-color: #f8f8f8;
  --background-brand-hover: #fff;
  --secondary-border-color: #9bb537;
  --modal-background-color-dark: #050505;
  --anim-time: 250ms cubic-bezier(.4, 0, .2, 1);
  --switch-dark: linear-gradient(#9bb537, rgba(155, 181, 55, .69));
}

.section-header {
  background: var(--primary-brand-color);
}

.header-container.container {
  min-width: 320px;
}

.header-items {
  align-items: center;
  padding-top: 20.5px;
  display: flex;
}

.header-nav {
  display: none;
}

.header-logo {
  font-size: 16px;
  font-weight: 600;
  line-height: 1.13;
}

.logo-light {
  color: var(--secondary-border-color);
}

.dark-logo {
  color: var(--modal-background-color-dark);
}

.header-icons {
  align-items: center;
  gap: 14px;
  margin-left: auto;
  display: flex;
}

.order-button.button {
  stroke: #050505;
  fill: none;
  width: 32px;
  height: 32px;
  transition: var(--anim-time);
  background-color: rgba(0, 0, 0, 0);
}

.icon-shopping-cart {
  color: var(--modal-background-color-dark);
  transition: var(--anim-time);
  background-color: rgba(0, 0, 0, 0);
}

.icon-shopping-cart:is(:hover, :focus) {
  stroke: var(--secondary-border-color);
}

.header-switch {
  display: none;
}

.menu-open-icon {
  stroke: var(--modal-background-color-dark);
  background-color: var(--primary-brand-color);
  transition: var(--anim-time);
}

.menu-open-icon:is(:hover, :focus) {
  stroke: var(--secondary-border-color);
}

.menu-open-btn {
  background-color: var(--primary-brand-color);
  transform: translateX(0);
}

.mobile-menu {
  z-index: 20;
  width: 53.33%;
  height: 100%;
  background: var(--secondary-border-color);
  transition: transform var(--anim-time), opacity var(--anim-time), visibility var(--anim-time);
  position: fixed;
  top: 0;
  left: 23%;
  overflow: auto;
}

.mob-menu-btn-close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition: transform .3s ease-in-out;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mob-menu-btn-close .mob-menu-close {
  transition: transform .3s ease-in-out;
}

.mob-menu-btn-close:hover .mob-menu-close {
  stroke: var(--background-brand-hover);
  transform: scale(1.5);
}

.mob-menu-close-icon {
  stroke: var(--primary-brand-color);
}

.mob-menu-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  padding-top: 200px;
  padding-bottom: 566px;
  display: flex;
}

.header-list-item-home {
  color: var(--background-brand-hover);
  justify-content: center;
  font-size: 12px;
  font-weight: 500;
}

.header-list-item {
  color: var(--primary-brand-color);
  justify-content: center;
  font-size: 12px;
  font-weight: 400;
}

.active-header-list-item {
  color: var(--modal-background-color-dark);
}

.switch {
  width: 46px;
  height: 20px;
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background-color: var(--switch-light-grey);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 15px;
  width: 15px;
  background-color: var(--white-2);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 3px;
  left: 4px;
}

input:checked + .slider {
  background: linear-gradient(#9bb537, rgba(155, 181, 55, .69));
  box-shadow: 5px 5px 15px rgba(243, 243, 243, .25);
}

input:checked + .slider:before {
  transform: translateX(26px);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.current:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: linear-gradient(#9bb537, rgba(155, 181, 55, .69));
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-link-active {
  color: var(--secondary-border-color);
}

.shopping-icon-use {
  fill: var(--background-brand-hover);
  stroke: var(--modal-background-color-dark);
}

@media screen and (max-width: 767px) {
  .header-list {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }

  .header-items {
    justify-content: space-between;
    padding-top: 28px;
  }

  .header-nav {
    display: inline;
    position: relative;
  }

  .header-link-home {
    color: var(--modal-background-color-dark);
    margin-right: 16px;
    font-size: 12px;
    font-weight: 500;
  }

  .header-link-active {
    color: var(--secondary-border-color);
  }

  .header-link-favorites {
    color: var(--modal-background-color-dark);
    font-size: 12px;
    font-weight: 400;
    position: relative;
  }

  .header-logo {
    justify-content: center;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.11;
    display: flex;
  }

  .header-icons {
    align-items: center;
    gap: 18px;
    margin-left: 0;
    display: flex;
  }

  .menu-open-btn {
    display: block;
  }

  .mobile-menu {
    display: none;
  }

  .header-switch {
    width: 46px;
    height: 20px;
    border-radius: 10px;
    display: flex;
    position: relative;
    box-shadow: 7px 5px 15px rgba(160, 158, 160, .4);
  }

  .header-switch input {
    display: none;
  }

  .header-slider {
    cursor: pointer;
    background-color: var(--switch-light-grey);
    border-radius: 34px;
    transition: all .4s;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
  }

  .header-slider:before {
    content: "";
    height: 17px;
    width: 17px;
    background-color: var(--white-2);
    border-radius: 50%;
    transition: all .4s;
    position: absolute;
    bottom: 2px;
    left: 2px;
  }

  .header-switch input:checked + .header-slider {
    background: linear-gradient(#9bb537, rgba(155, 181, 55, .69));
    box-shadow: 5px 5px 15px rgba(243, 243, 243, .25);
  }

  .header-switch input:checked + .header-slider:before {
    transform: translateX(26px);
    box-shadow: 5px 5px 15px rgba(243, 243, 243, .25);
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 100px;
  }

  .header-items {
    padding-top: 40px;
  }

  .header-icons {
